.row {
    margin: 5px;
    width: calc(100% - 10px);
    clear: both;
}
.block {
    display: inline-block;
    background-color: #EEEEFE;
    border-radius: 15px;
    min-height: 15px;
    max-width: 70%;
    margin-top: 5px;
    padding: 7px;
    padding-left: 10px;
    font-size: 14px;
}
.block.left {
    float: left;
}
.block.right {
    float: right;
}
.author {
    font-weight: bold;
    color: #488EDF;
    font-size: 12px;
}
.text {
    margin-top: 4px;
}
.time {
    font-size: 8px;
    margin-left: 5px;
    margin-right: 5px;
    float: right;

}